.tabs {
  position: fixed;
  top: 33px;             
  left: 200px;           
  height: 40px;          
  width: 100%;
  background: var(--sidebar);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  z-index: 90;      
  border-bottom: 1px solid var(--aside);
  margin-left: 50px;
  font-size: 1rem;
  overflow-x: auto; 
  white-space: nowrap;
}

@media (max-width: 57rem) {
  .tabs {
    grid-column: 1 / -1;
    left: 0;
    width: 100%;
    margin-left: 0px;
  }
}

@media (max-width: 28rem) {
  .tabs {
    overflow-x: scroll;
    margin-left: 0px;
  }
}

.filler {
  width: 100%;
  height: 100%;
  border-bottom: 1px solid var(--aside);
}

.tabList {
  height: 100%;
  list-style: none;
  display: flex;
  width: 100%;
}

.link {
  height: 100%;
  width: 100%;
  padding: 0 1.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 1px solid var(--aside);
  border-top: 2px solid transparent;
  border-bottom: 1px solid var(--aside);
  transition: all 0.2s;
}

.link:link,
.link:visited {
  text-decoration: none;
  color: var(--accent-4);
  transition: color 0.2s;
}

.tab:hover .link {
  color: var(--text);
}

.linkActive:link,
.linkActive:visited {
  color: var(--text);
  background: var(--main-bg);
  border-top: 2px solid var(--accent-1);
  border-bottom: 1px solid var(--main-bg);
}
