.sidebar {
  background: var(--sidebar);
  width: 250px;
  height: 100vh;
  position: fixed;
  top: 10;
  left: 0;
  border-right: 1px solid var(--aside);
  padding: 0.5rem 1rem;
}


.folders {
  position: sticky;
  top: 2;
  left: 0;
  text-transform: uppercase;
  margin-bottom: 0.7rem;
}

.folder {
  margin-left: 0.5rem;
  margin-bottom: 0.7rem;
}

.sideNav {
  list-style: none;
  margin-left: 1rem;
}

.sideNav:first-child {
  margin-bottom: 1rem;
}

.navItem:link,
.navItem:visited {
  color: var(--text);
  text-decoration: none;
  margin-left: 1.2rem;
  margin-bottom: 0.7rem;
  transition: color 0.2s;
  display: block;
}

.navItem:hover {
  color: white;
}

/* .navItem:active {
  color: var(--accent-1);
} */

@media (max-width: 57rem) {
  .sidebar {
    display: none;
  }
}
